'use client';

import React, { useState, useEffect, useRef } from 'react';
import { useSearchParams } from 'next/navigation';
import Image from 'next/image';
import { Bodoni_Moda, Lato } from 'next/font/google';
import Card from './Card';

const bodoni = Bodoni_Moda({
  subsets: ['latin'],
  display: 'swap',
});

const lato = Lato({
  weight: ['300', '700'],
  style: ['normal', 'italic'],
  subsets: ['latin'],
});

export default function PersonGallery({ persons }: { persons: any[] }) {
  const [currentIndex, setCurrentIndex] = useState(0);
  const [visibleCards, setVisibleCards] = useState(1);
  const containerRef = useRef<HTMLDivElement>(null);

  const searchParams = useSearchParams();
  const slugTwo = searchParams.get('slug');

  const updateVisibleCards = () => {
    if (window.innerWidth >= 1280) setVisibleCards(4); // Large screens
    else if (window.innerWidth >= 1024) setVisibleCards(3); // Desktop
    else if (window.innerWidth >= 768) setVisibleCards(2); // Tablet
    else setVisibleCards(1); // Mobile
  };

  useEffect(() => {
    updateVisibleCards();
    window.addEventListener('resize', updateVisibleCards);

    return () => {
      window.removeEventListener('resize', updateVisibleCards);
    };
  }, []);

  useEffect(() => {
    if (slugTwo && persons.length > 0) {
      const index = persons.findIndex((person) => person.slug.current === slugTwo);
      if (index !== -1) setCurrentIndex(index);
    }
  }, [slugTwo, persons]);

  const handlePrev = () => {
    setCurrentIndex((prev) => (prev === 0 ? persons.length - 1 : prev - 1));
  };

  const handleNext = () => {
    setCurrentIndex((prev) => (prev === persons.length - 1 ? 0 : prev + 1));
  };

  return (
    <div className="mt-8 mx-auto items-center">
      <div className="relative px-6 max-w-[1200px]" ref={containerRef}>
        {/* Card Container */}
        <div className="overflow-hidden">
          <div
            className="flex transition-transform duration-500 ease-in-out"
            style={{
              transform: `translateX(-${currentIndex * (100 / visibleCards)}%)`,
            }}
          >
            {persons.map((person, index) => (
              <div
                key={index}
                className="flex-none card px-2"
                style={{
                  width: `${100 / visibleCards}%`,
                }}
              >
                <Card person={person} />
              </div>
            ))}
          </div>
        </div>

        {/* Left Arrow */}
        <button
          className="absolute left-4 bottom-[-30px] z-20"
          onClick={handlePrev}
        >
          <Image src="/assets/arrow_left.png" alt="Previous Arrow" width={30} height={30} />
        </button>

        {/* Right Arrow */}
        <button
          className="absolute right-4 bottom-[-30px] z-20"
          onClick={handleNext}
        >
          <Image src="/assets/arrow_right.png" alt="Next Arrow" width={30} height={30} />
        </button>
      </div>
    </div>
  );
}
